@keyframes slidein {
    from{top: -100%;}
    to{top: 0;}
}
.titlecontainer{
    position: absolute;
    z-index: 3;
    background-color: black;
    border-radius: 10px;
    top: 15px;
    left: 25px;
    padding: 10px;
    box-shadow: 0px 10px 10px #00000035;
    
}
.title{
    color: white;
    text-align: center;
    padding: 0;
    margin: 0;
    font-size: 80px;
}
.fullname{
    color: white;
    text-align: center;
    font-size: 30px;
}
.container{
    position: relative;
    z-index:3;
    cursor: pointer;
    width: 100%;
    height: 90px;
    background-color: white;
    box-shadow: 0px 10px 10px #00000035;
    position: absolute;
    top: 0;
    left: 0;
    animation: slidein .25s ease;
}
.linkscontainer{
    height: 100%;
    list-style-type: none;
    display: flex;
    gap: 30px;
    padding-right: 30px;
    justify-content: flex-end;
    align-items: center;
}
.links{
    text-decoration: none;
    color: black;
    border-radius: 10000px;
    background-color: white;
    padding: 10px 20px;
  }
.links:hover{
    background-color: black;
    color: white;
}
.active{
    text-decoration: none;
    color: white;
    border-radius: 10000px;
    background-color: black;
    padding: 10px 20px;
}
.personalization{
    background-color: black;
    padding: 5px 30px;
    position: relative;
    top: 0px;
    color: white;
    display: flex;
    justify-content: flex-end;
    box-shadow: 0px 10px 10px #00000035;
}