@keyframes show {
    from{left: -100%;}
    to{left: 0;}
}
.container{
    position: relative;
    top: 90px;
    left: 0;
    text-align: center;
    padding: 30px;
    animation: show .5s ease;
}
.plainlist{
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    width: 100%;
}
.plainlistitem{
    font-size: 20px;
    padding: 30px;
    margin-bottom: 10px;
    border: 1px solid black;
    border-radius: 5px;
    width: 100%;
    background-color: white;
    color: black;
    display: flex;
    justify-content: space-evenly;
    gap: 50px;
}
.plainlistitem p{
    text-align: left;
    width: 200px;
}
.button{
    border: none;
    background-color: black;
    color: white;
    padding: 10px;
    margin: 10px;
    border-radius: 5px;
    font-size: 20px;
    transition: .25s;
}
.button:hover{
    transition: .25s;
    cursor: pointer;
    background-color: #00000070;
}
.buttoninactive{
    pointer-events: none;
    border: none;
    background-color: lightgray;
    color: white;
    padding: 10px;
    margin: 10px;
    border-radius: 5px;
    transition: .25s;  
}
.buttondelete{
    border: none;
    background-color: rgb(201, 105, 70);
    color: white;
    padding: 10px;
    margin: 10px;
    border-radius: 5px;
    font-size: 20px;
    transition: .25s;
}
.buttondelete:hover{
    transition: .25s;
    font-size: 30px;
    cursor: pointer;
    background-color: red;
}
.buttondelete2{
    border: none;
    background-color: red;
    color: white;
    padding: 10px;
    margin: 10px;
    border-radius: 5px;
    font-size: 20px;
    transition: .25s;
}
.buttondelete2:hover{
    font-size: 30px;
    transition: .25s;
}