@keyframes move-in {
    0% {left: -100%;}
    90% {left: -100%;}
    100%{left: 50%;}
}
@keyframes scaleup {
    0% {left: 0%; transform: translate(-50%, -50%);}
    50% {left: 50%; transform: translate(-50%, -50%);}
    95% {left: 50%; transform: translate(-50%, -190%);}
    100% {left: 50%; transform: translate(-50%, -190%);}
}
@keyframes scaleupout {
    0% {left: 0%; transform: translate(-100%, -50%);}
    50% {left: 50%; transform: translate(-50%, -50%);}
    95% {left: 50%; transform: translate(-50%, -190%);}
    100% {left: 50%; transform: translate(-50%, -190%);}
}
@keyframes fadein{
    0% {left: -100%;}
    50% {left: -50%;}
    100% {left: -50%;}
    
}
@keyframes fadeout{
    0% {left: -100%;}
    50% {left: -50%;}
    100% {left: -50%;}
    
}
.bg{
    position: absolute;
    top: 0;
    left: -50%;
    height: 100%;
    width: 100%;
    transform: translateX(0);
    background-color: #000000;
    animation: fadein 1.5s;
}
.bgmoveout{
    position: absolute;
    top: 0;
    left: -100%;
    height: 100%;
    width: 100%;
    transform: translateX(0);
    background-color: #000000;
    animation: fadeout 1.5s reverse;
}
.titlecontainer{
    position: absolute;
    background-color: black;
    border-radius: 10px;
    top: 50%;
    left: 50%;
    padding: 20px;
    transform: translate(-50%, -190%);
    animation-name: scaleup;
    animation-duration: 1.5s;
}
.titlecontainermoveout{
    position: absolute;
    background-color: black;
    border-radius: 10px;
    top: 50%;
    left: 0%;
    padding: 20px;
    transform: translate(-100%, -50%);
    animation-name: scaleupout;
    animation-duration: 1.5s;
    animation-direction: reverse;
}
.title{
    color: white;
    text-align: center;
    padding: 0;
    margin: 0;
    font-size: 100px;
}
.fullname{
    color: white;
    text-align: center;
    font-size: 30px;
}
.cardcontainer{
    position: absolute;
    background-color: lightgray;
    top: 50%;
    left: 50%;
    width: 250px;
    padding: 20px;
    border-radius: 5px;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition: .5s;
    animation-name: move-in;
    animation-duration: 1.6s;
}
.moveout{
    position: absolute;
    top: 50%;
    left: -100%;
    width: 250px;
    padding: 10px;
    border-radius: 10px;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.buttoninactive{
    pointer-events: none;
    border: none;
    background-color: darkgray;
    color: white;
    padding: 10px;
    margin: 10px;
    border-radius: 5px;
    font-size: 16px;
}
.button{
    border: none;
    background-color: black;
    color: white;
    padding: 10px;
    margin: 10px;
    border-radius: 5px;
}
.button:hover{
    background-color: teal;
}
input{
    font-size: 16px;
    padding: 5px;
    border: none;
    outline: none;
}
label{
    font-size: 20px;
}
.links{
    display: flex;
    justify-content: center;
}
.links a{
    text-decoration: none;
    color: black;
    padding: 5px;
    margin: 5px;
    border-radius: 5px;
    font-size: 20px;
}
.links a:hover{
    background-color: black;
    color: white;
}
#message{
    font-size: 20px;
    color: #00000000;
}
#message-visible{
    text-align: center;
    font-size: 20px;
    color: #000000;
}
.plainlist{
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    width: 100%;
    background-color: black;
    color: white;
}
.plainlistitem{
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
    width: 100%;
    background-color: blue;
    color: white;
}