@keyframes show {
    from{left: -100%;}
    to{left: 0;}
}
.container{
    position: relative;
    top: 90px;
    left: 0;
    padding: 20px;
    animation: show .5s ease;
}
.plainlistitemtitle{
    font-size: 30px;
    padding: 30px;
    margin-bottom: 10px;
    border: 1px solid black;
    border-radius: 5px;
    width: 100%;
    background-color: white;
    color: black;
    display: flex;
    justify-content: space-evenly;
    gap: 50px;
}
.depositlistitem{
    padding: 30px;
    margin-bottom: 10px;
    border: 1px solid black;
    border-radius: 5px;
    width: 100%;
    background-color: white;
    color: teal;
    display: flex;
    justify-content: space-evenly;
}
.withdrawlistitem{
    padding: 30px;
    margin-bottom: 10px;
    border: 1px solid black;
    border-radius: 5px;
    width: 100%;
    background-color: white;
    color: rgb(229, 38, 38);
    display: flex;
    justify-content: space-evenly;
}
.plainlistitem p{
    text-align: left;
    width: 200px;
}