@keyframes show {
    from{left: -100%;}
    to{left: 0;}
}
.balancecontainer{
    padding: 30px;
    width: 60%;
    min-width: 300px;
}
.movementscontainer{
    width: 50%;
    min-width: 300px;
}
.container{
    position: relative;
    top: 110px;
    left: 0;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 30px;
    animation: show .5s ease;
}
.cardcontainer{
    padding: 30px;
    width: 50%;
    min-width: 300px;
    background-color: lightgray;
}
#message{
    font-size: 20px;
    color: #00000000;
}
#message-visible{
    font-size: 20px;
    text-align: center;
    color: #000000;
}
.buttoninactive{
    pointer-events: none;
    border: none;
    background-color: darkgray;
    color: white;
    padding: 10px;
    margin: 10px;
    border-radius: 5px;
    font-size: 16px;
}
.button{
    border: none;
    background-color: black;
    color: white;
    padding: 10px;
    margin: 10px;
    border-radius: 5px;
    font-size: 16px;
}
.button:hover{
    background-color: teal;
}
.plainlist{
    width: 100%;
}
.plainlistitemtitle{
    font-size: 30px;
    padding: 30px;
    margin-bottom: 10px;
    border: 1px solid black;
    border-radius: 5px;
    width: 100%;
    min-width: 300px;
    background-color: white;
    color: black;
    display: flex;
    justify-content: space-evenly;
    gap: 50px;
}
.depositlistitem{
    padding: 30px;
    margin-bottom: 10px;
    border: 1px solid black;
    border-radius: 5px;
    width: 100%;
    background-color: white;
    color: teal;
    display: flex;
    justify-content: space-evenly;
}
.withdrawlistitem{
    padding: 30px;
    margin-bottom: 10px;
    border: 1px solid black;
    border-radius: 5px;
    width: 100%;
    background-color: white;
    color: rgb(229, 38, 38);
    display: flex;
    justify-content: space-evenly;
}
.plainlistitem p{
    text-align: left;
    width: 200px;
}
.processing{
    top: 0;
    left: 0;
    z-index: 5;
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: #00000030;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}
.processing h1{
    background-color: white;
    box-shadow: 0 10px 10px #00000050;
    border-radius: 5px;;
    color: black;
    padding: 70px 150px;
}
.transactiontitle{
    border-radius: 5px;
    padding: 10px;
    margin: 10px 10px 30px 10px;
    color: teal;
    font-size: 30px;
}