@keyframes slidein {
    from{top: -500px;}
    to{top: 200px;}
}
@keyframes slideintitle {
    from{top: -500px;}
    to{top: 0px;}
}
.title{
    position: relative;
    animation: slideintitle .25s ease;
    top: 0px;
}
.titlewords{
    background-color: black;
    padding: 50px;
    padding-bottom: 0px;
    color: white;
    font-size: 40px;
    text-align: center;
}
.cardcontainer{
    position: relative;
    background-color: lightgray;
    top: 200px;
    left: 50%;
    width: 320px;
    padding: 20px 50px;
    border-radius: 5px;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition: .5s;
    animation: slidein .25s ease;
    box-shadow: 0 10px 10px #00000050;
}
.moveout{
    position: absolute;
    top: 50%;
    left: -100%;
    width: 250px;
    padding: 10px;
    border-radius: 10px;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.button{
border: none;
background-color: black;
color: white;
padding: 10px;
margin: 10px;
border-radius: 5px;
}
.button:hover{
background-color: teal;
}
input{
font-size: 16px;
padding: 5px;
border: none;
outline: none;
}
label{
font-size: 20px;
}
.links{
display: flex;
justify-content: space-between;
}
.links a{
text-decoration: none;
color: black;
padding: 5px;
margin: 5px;
border-radius: 5px;
}
.links a:hover{
background-color: black;
color: white;
}
#message{
font-size: 20px;
color: #00000000;
}
#message-visible{
font-size: 20px;
color: #000000;
}